import React from "react";
import { Link } from "react-router-dom";

function Fallback() {
  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <h1>
        <Link to="/">{"<<"}Back to HomePage</Link>
      </h1>
    </div>
  );
}

export default Fallback;
